@use "viewports.scss" as v;

.show-more {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  padding: 0 20px 25px 20px;
  text-align: center;
  background-image: url("../img/show-more-mobile.svg");
  background-repeat: no-repeat;
  background-position: 50% 25px;

  @media (min-width: v.$tablet-width) {
    margin-top: 82px;
    min-height: 429px;
    padding-bottom: 39px;
    padding-left: 38px;
    padding-right: 37px;
    background-image: url("../img/show-more-tablet.svg");
    background-position: 50% 48px;
    background-color: #ffffff;
    border: 2px solid #f2f2f2;
    justify-content: end;
  }

  @media (min-width: v.$desktop-width) {
    padding-left: 36px;
    padding-right: 36px;
  }
}

.show-more__title {
  margin: 0;
  padding: 0;
  padding-top: 78px;
}

.show-more__text {
  margin: 0;
  margin-top: 9px;
  padding: 0;

  @media (min-width: v.$tablet-width) {
    margin-top: 20px;
  }

  @media (min-width: v.$desktop-width) {
    margin-left: 0;
    margin-right: 0;
  }
}
